.primary-header {
  position: relative;
  height: 60vh;
  min-height: 500px;
  background-color: var(--primary-color);
}

/* Slider Container */
.slider {
  margin: auto;
  overflow: hidden;
  z-index: 1;
}

/* Slides Wrapper */
.slides {
  display: flex;
  transition: transform 1s ease-in-out;
}

/* Individual Slide */
.slide {
  min-width: 100%;
  box-sizing: border-box;
  position: relative;
}
.slide::after {
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: var(--black-color);
  position: absolute;
  z-index: 1;
  opacity: 0.2;
}
.slide > p {
  z-index: 2;
  background: rgba(0, 0, 0, 0.3882352941);
  padding: 8px 15px;
  font-size: 14px;
  opacity: 0;
  transition-delay: 0s;
  width: 100%;
  text-align: center;
}

.slide.slider-active > p {
  opacity: 1;
  transition-delay: 1.5s;
}

.site-logo {
  aspect-ratio: 1/1;
  width: clamp(70px, 6.25vw, 120px);
  background-color: var(--white-color);
}

.llisting > a {
  position: relative;
  z-index: 2;
}

.includs-submenu {
  cursor: pointer;
}
.includs-submenu:hover img.nav-icon {
  transform: rotate(-180deg);
}

.sub-menu {
  width: max-content;
  cursor: auto;
  z-index: -2;
  visibility: hidden;
  opacity: 0;
  transform: translateY(20px);
  transition-property: opacity, transform, visibility;
}
.sub-menu::after {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: calc(100% - 39px);
  background-color: var(--secondary-color);
  z-index: -1;
  content: "";
  border-radius: 0px 0px 8px 8px;
  box-shadow: 0px 24px 120px 0px rgba(0, 0, 0, 0.1882352941);
}
.sub-menu.active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
  transform: translateY(0px);
}
.sub-menu > li {
  width: 100%;
  display: flex;
}
.sub-menu > li > a {
  width: 100%;
  display: flex;
  color: var(--white-color);
}

img.nav-icon {
  width: 15px;
  height: 13px;
  object-fit: cover;
  margin-left: 5px;
}

.site-main {
  height: 200vh;
}

header .anchor {
  font-size: 18px;
}

@media (max-width: 1024px) {
  .primary-header {
    min-height: 350px;
    height: 40vh;
  }
  .burger-trigger {
    width: 28px;
    height: 2px;
  }
  .mobile-burger.active {
    transform: rotate(90deg);
  }
  .mobile-burger.active > .burger-trigger {
    opacity: 0;
  }
  .mobile-burger.active > .burger-trigger:first-child {
    transform: rotate(45deg) translate(9px, 8px);
    opacity: 1;
  }
  .mobile-burger.active > .burger-trigger:last-child {
    transform: rotate(-45deg) translate(8px, -8px);
    opacity: 1;
  }
  .mobile-nav > p {
    font-size: 18px;
  }
}